html, body {
  overflow-x: hidden;
  overflow-y: visible;
  position: relative;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }

.demo {
  background-color: #ff9205;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding-bottom: 10rem;
  margin: 0; }
  .demo__meta {
    padding: 0 2rem;
    color: white; }
    @media only screen and (max-width: 500px) {
      .demo__meta {
        text-align: center; } }
  .demo__text {
    font-weight: 500;
    color: #e0e0e0;
    width: 85%;
    max-width: 900px;
    margin: 5rem auto 7rem;
    text-shadow: 1px 1px 0px #c91616; }
    .demo__text a {
      color: white;
      font-weight: bold; }

.target {
  cursor: pointer; }
  .target:hover {
    background-color: #eb4040; }
  .target__board {
    background-color: #e01818;
    width: 100px;
    height: 100px;
    margin: 12rem auto 5rem;
    border-radius: 50%;
    border: 5px solid white;
    display: flex;
    box-shadow: 0px 10px 25px -15px black; }
    .target__board h2 {
      align-self: center;
      color: white;
      text-align: center;
      width: 100%;
      pointer-events: none; }
  .target__box {
    display: inline-block;
    width: 100px;
    background-color: #e01818;
    color: white;
    font-weight: 500;
    padding: 1rem;
    text-align: center;
    border: 5px solid white;
    box-shadow: 0px 10px 25px -15px black;
    position: absolute; }
    .target__box span {
      pointer-events: none; }
    .target__box--left {
      left: 1rem; }
    .target__box--right {
      right: 1rem; }

.reference {
  background-color: white;
  min-width: 250px;
  min-height: 125px;
  border-radius: 5px;
  border: 5px solid #4e4e4e;
  display: flex;
  box-shadow: 0px 10px 25px -15px black; }
  .reference--static {
    width: 200px;
    height: 100px; }
  .reference--bleed {
    transform: translateX(200px); }
  .reference h2 {
    color: #4e4e4e;
    align-self: center;
    text-align: center;
    width: 100%; }
